import React from "react";
import { Link } from "react-router-dom";
import "../App.css";
import instaicon from "../icons/instagram-svgrepo-com.svg";
import facebookicon from "../icons/facebook-176-svgrepo-com.svg";
import tiktokicon from "../icons/tiktok-svgrepo-com.svg";

function SectionSeven() {
  return (
    <div className="section-7">
      <h3 className="text-19">About Us</h3>
      <a className="text-20">
        DEVSBASE is a coding community that teaches non developers to learn how
        to code and become a frontend developer from beginner to pro . Learn at
        home with DEVSBASE we make learning more easy and fun for you..
      </a>

      <div className="div-26">
        <Link className="btn-6" to="/about">
          More About US
        </Link>
      </div>

      <div className="div-27">
        <Link to="https://www.instagram.com/devsbase_/">
          <img src={instaicon} className="img-10" />
        </Link>

        <Link to="">
          <img src={facebookicon} className="img-10" />
        </Link>

        <Link to="https://www.tiktok.com/@devsbase">
          <img src={tiktokicon} className="img-10" />
        </Link>
      </div>

      <a className="text-21">devsbase@gmail.com</a>
    </div>
  );
}

export default SectionSeven;
