import React from "react";
import Header from "../userhome/Header";
import "../Userhome.css";
import subbanner from "../icons/subbanner.png";
import mark from "../icons/check-svgrepo-com.svg";
import cancel from "../icons/cancel-svgrepo-com.svg";
import { Link, useNavigate } from "react-router-dom";
import { useRef } from "react";
import Coursesicon from "../icons/video.png";
import exerciseicon from "../icons/exercises.png";
import recenticon from "../icons/recent.png";
import abouticon from "../icons/about.png";
import updateIcon from "../icons/updates.png";
import subicon from "../icons/sub.png";
import navIcon from "../icons/nav-icon-a-svgrepo-com.svg";

function Payment() {
  const displayNav = useRef();
  const headerDisplay = useRef();
  const handleClick = () => {
    displayNav.current.style.display = "flex";
    headerDisplay.current.style.display = "none";
  };

  const cancelnav = () => {
    displayNav.current.style.display = "none";
    headerDisplay.current.style.display = "flex";
  };

  const navigate = useNavigate();

  const Logout = () => {
    window.localStorage.removeItem("isLogedIn");

    navigate("/");
  };
  return (
    <div className="payment-page">
      <div className="header-2-popup" ref={displayNav}>
        <div className="header-2-popup-2">
          <div className="header-2-div-2">
            <h2>DevsBase</h2>
            <h3 onClick={cancelnav}>x</h3>
          </div>

          <div className="header-2-div-3">
            <h3>
              Hi, <span>Taofeeqcodes</span>
            </h3>
          </div>

          <div className="header-2-div-4">
            <h3>Pages</h3>

            <Link to="/seriespage" className="header-2-div-5">
              <img src={Coursesicon} />
              <h2>Courses</h2>
            </Link>

            <Link to="/exercises" className="header-2-div-5">
              <img src={exerciseicon} />
              <h2>Exercises</h2>
            </Link>

            <Link to="/recently" className="header-2-div-5">
              <img src={recenticon} />
              <h2>Recently</h2>
            </Link>

            <Link to="/Paymentpage" className="header-2-div-5">
              <img src={subicon} />
              <h2>Subscription</h2>
            </Link>

            <Link to="/updatespage" className="header-2-div-5">
              <img src={updateIcon} />
              <h2>Updates</h2>
            </Link>

            <Link to="/About" className="header-2-div-5">
              <img src={abouticon} />
              <h2>About Us</h2>
            </Link>

            <a>Version 1.0</a>

            <a onClick={() => Logout()}>Log Out</a>
          </div>
        </div>
      </div>
      <div className="header-2" ref={headerDisplay}>
        <div className="header-2-div-1">
          <h1>Subcription</h1>

          <img src={navIcon} onClick={handleClick} />
        </div>
      </div>
      <h3 className="payment-page-text-1">
        Current <span>Subscription</span>
      </h3>

      <div className="payment-page-div-1">
        <div className="payment-page-div-2">
          <div className="payment-page-div-3">
            <h3>
              DEVS<span>BASE</span>
            </h3>

            <a className="payment-page-text-2">Lite Subscription Plan</a>
          </div>
          <div className="payment-page-div-3">
            <img src={subbanner} />
          </div>
        </div>
      </div>

      <div className="payment-page-section-2">
        <h3 className="payment-page-text-4">DEVSBASE Subscription plans</h3>

        <div className="div-19">
          <div className="div-20">
            <div className="div-22">
              <h3 className="text-16">DEVSBASE PRO</h3>

              <h2 className="text-17">$10.5</h2>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                Pay $10.5 dollars and get 120 plus courses
              </a>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                get 58 exercises in total to practice with{" "}
              </a>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                10 landing pages to build and practice with
              </a>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">get the developers road map</a>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                get the react native course for app development
              </a>
            </div>

            <div className="div-28">
              <Link className="btn-7">not Available yet</Link>
            </div>
          </div>
          <div className="div-21">
            <div className="div-23">
              <h3 className="text-16">DEVSBASE LITE</h3>
              <h2 className="text-17">$5</h2>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                Pay $10.5 dollars and get 80 plus courses
              </a>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                get 32 exercises in total to practice with
              </a>
            </div>

            <div className="div-24">
              <img src={mark} className="img-11" />
              <a className="text-18">
                5 landing pages to build and practice with
              </a>
            </div>

            <div className="div-24">
              <img src={cancel} className="img-11" />
              <a className="text-18">get the developers road map</a>
            </div>

            <div className="div-24">
              <img src={cancel} className="img-11" />
              <a className="text-18">
                get the react native course for app development
              </a>
            </div>

            <div className="div-28">
              <Link to="/" className="btn-7">
                Subcribe Now
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Payment;
