import React, { useState } from "react";
import searchIcon from "../icons/search-alt-svgrepo-com.svg";
import { VideoLink } from "../Vids";

import Searchtable from "../Searchtable";

function SearchPage() {
  const [query, setQuery] = useState("");
  const search = (data) => {
    return data.filter(
      (item) =>
        item.video_name.toLowerCase().includes(query) ||
        item.series.toLowerCase().includes(query)
    );
  };

  return (
    <div className="searchpage">
      <h3 className="searchpage-text-1">Search Box</h3>
      <div className="searchpage-header">
        <div className="searchpage-div-1">
          <input
            type="text"
            placeholder="search all leasons"
            onChange={(e) => setQuery(e.target.value)}
          />
          <img src={searchIcon} className="img-1" />
        </div>
      </div>

      <ul className="searchpage-list">
        <Searchtable data={search(VideoLink)} />
      </ul>
    </div>
  );
}

export default SearchPage;
