import React from "react";
import { Link } from "react-router-dom";
import htmlbanner from "../img/htmlBanner.jpg";
import htmllesson1 from "../thumbnails/html lesson-1.png";
import htmllesson2 from "../thumbnails/html lesson-2.png";
import htmllesson3 from "../thumbnails/html lesson-3.png";
