import React from "react";
import "../Aboutus.css";
import SectionThree from "../components/SectionThree";
import { useRef } from "react";
import Coursesicon from "../icons/video.png";
import exerciseicon from "../icons/exercises.png";
import recenticon from "../icons/recent.png";
import abouticon from "../icons/about.png";
import updateIcon from "../icons/updates.png";
import subicon from "../icons/sub.png";
import navIcon from "../icons/nav-icon-a-svgrepo-com.svg";
import { Link, useNavigate } from "react-router-dom";

function Aboutus() {
  const displayNav = useRef();
  const headerDisplay = useRef();
  const handleClick = () => {
    displayNav.current.style.display = "flex";
    headerDisplay.current.style.display = "none";
  };

  const cancelnav = () => {
    displayNav.current.style.display = "none";
    headerDisplay.current.style.display = "flex";
  };

  const navigate = useNavigate();

  const Logout = () => {
    window.localStorage.removeItem("isLogedIn");

    navigate("/");
  };
  return (
    <div className="aboutus">
      <div className="header-2-popup" ref={displayNav}>
        <div className="header-2-popup-2">
          <div className="header-2-div-2">
            <h2>DevsBase</h2>
            <h3 onClick={cancelnav}>x</h3>
          </div>

          <div className="header-2-div-3">
            <h3>
              Hi, <span>Taofeeqcodes</span>
            </h3>
          </div>

          <div className="header-2-div-4">
            <h3>Pages</h3>

            <Link to="/seriespage" className="header-2-div-5">
              <img src={Coursesicon} />
              <h2>Courses</h2>
            </Link>

            <Link to="/exercises" className="header-2-div-5">
              <img src={exerciseicon} />
              <h2>Exercises</h2>
            </Link>

            <Link to="/recently" className="header-2-div-5">
              <img src={recenticon} />
              <h2>Recently</h2>
            </Link>

            <Link to="/Paymentpage" className="header-2-div-5">
              <img src={subicon} />
              <h2>Subscription</h2>
            </Link>

            <Link to="/updatespage" className="header-2-div-5">
              <img src={updateIcon} />
              <h2>Updates</h2>
            </Link>

            <Link to="/About" className="header-2-div-5">
              <img src={abouticon} />
              <h2>About Us</h2>
            </Link>

            <a>Version 1.0</a>

            <a onClick={() => Logout()}>Log Out</a>
          </div>
        </div>
      </div>
      <div className="header-2" ref={headerDisplay}>
        <div className="header-2-div-1">
          <h1 className="text-1">About Us</h1>

          <img src={navIcon} onClick={handleClick} />
        </div>
      </div>
      <h3 className="aboutus-text-1">
        DEVS<span>BASE</span>
      </h3>

      <div className="aboutus-section-1">
        <a>
          DEVSBASE is a coding community that teaches non developers to learn
          how to code and become a frontend developer from beginner to pro .
          Learn at home with DEVSBASE we make learning more easy and fun for
          you.. Learn how to code using DEVSBASE courses to give you the best
          experince of coding with our premium Videos and get projects to build
          with exercises to practice with for fast learning . learning tech is
          more easy with DEVSBASE
          <br /> Version 1.0
        </a>

        <h3>Learn Now</h3>

        <SectionThree />
      </div>
    </div>
  );
}

export default Aboutus;
