import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "../Register.css";
import { useRef } from "react";
import showPassword from "../icons/eye-slash-svgrepo-com.svg";
import hidePassword from "../icons/eye-svgrepo-com.svg";
import axios from "axios";

function Login() {
  const showpass = useRef();
  const showpassIcon = useRef();
  const hidepassIcon = useRef();
  const errorOne = useRef();
  const errorTwo = useRef();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [username, setUsername] = useState("");
  const history = useNavigate();

  async function submit(e) {
    e.preventDefault();

    try {
      await axios
        .post("http://localhost:3001/", { email, password })
        .then((res) => {
          if (res.data == "exist") {
            history("/homepage", { state: { id: email } });
            // window.localStorage.setItem("isLogedIn", true);
          } else if (res.data == "notexist") {
            history("/homepage", { state: { id: email } });
            errorOne.current.style.display = "flex";
          }
        })
        .catch((e) => {
          // alert("wrong details");
          errorTwo.current.style.display = "flex";
        });
    } catch (e) {
      console.log(e);
    }
  }

  const showPassClick = () => {
    showpass.current.type = "text";
    hidepassIcon.current.style.display = "flex";
    showpassIcon.current.style.display = "none";
  };

  const hidePassClick = () => {
    showpass.current.type = "password";
    hidepassIcon.current.style.display = "none";
    showpassIcon.current.style.display = "flex";
  };

  const cancelError = () => {
    errorOne.current.style.display = "none";
    errorTwo.current.style.display = "none";
  };

  return (
    <div className="home-login-page">
      <form className="form-login-page">
        <h3 className="text-1-login-page">DEVSBASE Login</h3>

        <a className="error-text">User does not exist</a>
        <a ref={errorTwo} className="error-text">
          Wrong Details
        </a>

        <input
          type="text"
          onChange={(e) => {
            setEmail(e.target.value);
          }}
          placeholder="email"
          className="input-1-login-page"
        />

        <div className="div-1-login-page">
          <input
            type="password"
            onChange={(e) => {
              setPassword(e.target.value);
            }}
            placeholder="password"
            className="input-2-login-page"
            ref={showpass}
          />
          <img
            src={showPassword}
            onClick={showPassClick}
            ref={showpassIcon}
            className="login-page-img-1"
          />
          <img
            src={hidePassword}
            onClick={hidePassClick}
            ref={hidepassIcon}
            className="login-page-img-2"
          />
        </div>
        <Link to="/forgotpassword" className="text-2-login-page">
          Forgot password ?
        </Link>

        <button type="submit" onClick={submit} className="btn-1-login-page">
          Login
        </button>
      </form>

      <a className="text-3-login-page">
        dont have an account <Link to="/signup">Sign Up</Link>
      </a>
    </div>
  );
}

export default Login;
