import React from "react";
import { useRef } from "react";
import "../App.css";
import { Link } from "react-router-dom";
import searchIcon from "../icons/search-svgrepo-com.svg";
import navIcon from "../icons/nav-icon-a-svgrepo-com.svg";
import instaicon from "../icons/instagram-svgrepo-com.svg";
import facebookicon from "../icons/facebook-176-svgrepo-com.svg";
import tiktokicon from "../icons/tiktok-svgrepo-com.svg";

function Header() {
  const displayNav = useRef();
  const headerDisplay = useRef();
  const handleClick = () => {
    displayNav.current.style.display = "flex";
    headerDisplay.current.style.display = "none";
  };

  const cancelnav = () => {
    displayNav.current.style.display = "none";
    headerDisplay.current.style.display = "flex";
  };

  return (
    <React.Fragment>
      <div className="navbar" ref={displayNav}>
        <div className="div-11">
          <h3 onClick={cancelnav}>X</h3>
        </div>

        <div className="div-30">
          <Link to="/" className="btn-9">
            Home
          </Link>

          <Link to="/login" className="btn-8">
            Log in
          </Link>

          <Link to="/login" className="btn-8">
            Courses
          </Link>

          <Link to="/about" className="btn-8">
            About Us
          </Link>

          <div className="div-27">
            <Link to="https://www.instagram.com/devsbase_/">
              <img src={instaicon} className="img-10" />
            </Link>

            <Link to="">
              <img src={facebookicon} className="img-10" />
            </Link>

            <Link to="https://www.tiktok.com/@devsbase">
              <img src={tiktokicon} className="img-10" />
            </Link>
          </div>

          <a className="text-21">devsbase@gmail.com</a>
        </div>
      </div>

      <div className="header" ref={headerDisplay}>
        <div className="header-items">
          <div className="div-1">
            <h1 className="text-1">
              DEVS<span>BASE</span>
            </h1>
          </div>

          <div className="div-2">
            <Link to="/signup" className="text-2">
              Home
            </Link>
            <Link to="/login" className="text-3">
              Courses
            </Link>
            <Link to="/about" className="text-3">
              About us
            </Link>
          </div>

          <div className="div-3">
            <Link to="/searchpage"></Link>

            <Link to="/signup" className="btn-1">
              Sign UP
            </Link>
            <Link to="/login" className="btn-2">
              login
            </Link>

            <img src={navIcon} className="img-2" onClick={handleClick} />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Header;
