import "./App.css";
import { HashRouter, Routes, Route } from "react-router-dom";
import Login from "./pages/Login";
import Forgotpassword from "./pages/Forgotpassword";
import SignUp from "./pages/SignUp";
import Videos from "./pages/Videos";
import Payment from "./pages/Payment";
import Home from "./pages/Home";
import Aboutus from "./pages/Aboutus";
import SearchPage from "./pages/SearchPage";
import UserHome from "./pages/UserHome";
import Updates from "./pages/Updates";
import Inputcode from "./pages/Inputcode";
import Resetpassword from "./pages/Resetpassword";
import VideoPage from "./pages/VideoPage";
import CourseOne from "./pages/CourseOne";
import { R } from "react-html5video";
import Recently from "./pages/Recently";
import Exercises from "./pages/Exercises";
import CourseTwo from "./pages/CourseTwo";
import CourseThree from "./pages/CourseThree";
import CourseFour from "./pages/CourseFour";
import CourseFive from "./pages/CourseFive";

function App() {
  const login = window.localStorage.getItem("isLogedIn");

  return (
    <div className="App">
      <HashRouter>
        <Routes>
          <Route path="/" element={login ? <UserHome /> : <Home />} />
          <Route path="/login" element={login ? <UserHome /> : <Login />} />
          <Route path="/signup" element={login ? <UserHome /> : <SignUp />} />
          <Route path="/forgotpassword" element={<Forgotpassword />} />
          <Route path="/seriespage" element={<Videos />} />
          <Route path="/Paymentpage" element={<Payment />} />
          <Route path="/About" element={<Aboutus />} />
          <Route path="/searchpage" element={<SearchPage />} />
          <Route path="/homepage" element={<UserHome />} />
          {/* <Route path="/homepage" element={<UserHome />} /> */}
          <Route path="/updatespage" element={<Updates />} />
          <Route path="/Videopage" element={<VideoPage />} />
          <Route path="/inputcode" element={<Inputcode />} />
          <Route path="/resetpassword" element={<Resetpassword />} />
          <Route path="/courseone" element={<CourseOne />} />
          <Route path="/coursetwo" element={<CourseTwo />} />
          <Route path="/coursethree" element={<CourseThree />} />
          <Route path="/coursefour" element={<CourseFour />} />
          <Route path="/coursefive" element={<CourseFive />} />
          <Route path="/recently" element={<Recently />} />
          <Route path="/exercises" element={<Exercises />} />
        </Routes>
      </HashRouter>
    </div>
  );
}

export default App;
