import React from "react";
import { Link } from "react-router-dom";
import "../Register.css";
import { useRef } from "react";
import showPassword from "../icons/eye-slash-svgrepo-com.svg";
import hidePassword from "../icons/eye-svgrepo-com.svg";

function Resetpassword() {
  const showpass = useRef();
  const showpassTwo = useRef();
  const showpassIcon = useRef();
  const hidepassIcon = useRef();

  const showPassClick = () => {
    showpass.current.type = "text";
    showpassTwo.current.type = "text";
    hidepassIcon.current.style.display = "flex";
    showpassIcon.current.style.display = "none";
  };

  const hidePassClick = () => {
    showpass.current.type = "password";
    showpassTwo.current.type = "password";
    hidepassIcon.current.style.display = "none";
    showpassIcon.current.style.display = "flex";
  };
  return (
    <div className="home-login-page">
      <form className="form-login-page">
        <h3 className="text-1-login-page">Create Password</h3>

        <div className="div-1-login-page">
          <input
            type="password"
            placeholder="password"
            className="input-2-signup-page"
            ref={showpass}
          />
          <img
            src={showPassword}
            onClick={showPassClick}
            ref={showpassIcon}
            className="login-page-img-1"
          />
          <img
            src={hidePassword}
            onClick={hidePassClick}
            ref={hidepassIcon}
            className="login-page-img-2"
          />
        </div>

        <input
          type="password"
          placeholder="confirm password"
          className="input-1-signup-page"
          ref={showpassTwo}
        />

        <Link className="btn-1-login-page" to="/login">
          Create password
        </Link>
      </form>
    </div>
  );
}

export default Resetpassword;
