import React from "react";
import { Link } from "react-router-dom";
import "../App.css";
import nexticon from "../icons/next-svgrepo-com.svg";

import background from "../img/background.png";

function SectionOne() {
  return (
    <React.Fragment>
      <div className="section-1">
        <img src={background} className="img-3" />

        <div className="div-4">
          <h1 className="text-4">
            DEVS<span>BASE</span>
          </h1>

          <h3 className="text-5">
            Academy to learn Coding from
            <br />
            <span> Beginner To pro ....</span>
          </h3>

          <div className="div-5">
            <a className="text-6">Become a fullstack dev</a>
            <Link to="/signup" className="btn-3">
              Register Now
              <img src={nexticon} className="img-12" />
            </Link>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default SectionOne;
