import React from "react";
import { Link } from "react-router-dom";
import "../Register.css";

function Inputcode() {
  return (
    <div className="home-login-page">
      <form className="form-inputcode-page">
        <h3 className="text-1-login-page">Input your code </h3>

        <input
          type="text"
          placeholder="input code"
          className="input-1-forgot-password-page"
        />

        <a className="text-2-login-page">Didn't get a code ?</a>

        <Link className="btn-1-login-page" to="/resetpassword">
          Reset password
        </Link>
      </form>

      <a className="text-3-login-page">
        i know my password <Link to="/login">login</Link>
      </a>
    </div>
  );
}

export default Inputcode;
