import React from "react";
import "./App.css";
import { Link } from "react-router-dom";

const Searchtable = ({ data }) => {
  return (
    <table className="table-class">
      <tbody>
        <tr className="search-result-1">
          <th className="search-result">video name</th>
          <th className="search-result">video description</th>
          <th className="search-result">series</th>
        </tr>

        {data.map((item) => (
          <Link to={item.link} className="search-result-1">
            <tr key={item.id} className="search-result">
              <td className="result">{item.video_name}</td>
              <td className="result">{item.discription}</td>
              <td className="result">{item.series}</td>
            </tr>
          </Link>
        ))}
      </tbody>
    </table>
  );
};

export default Searchtable;
