import React from "react";
import bannerone from "../img/startlearning.jpg";
import bannertwo from "../img/buildingprojects.jpg";
import bannerthree from "../img/learnathome.jpg";
import "../App.css";

function SectionFive() {
  return (
    <div className="section-5">
      <h3 className="text-11">Why DEVSBASE is more fun to learn from</h3>
      <div className="div-17">
        <div className="div-18">
          <img src={bannerone} className="img-6" />

          <h3 className="text-13">Beginner Friendly</h3>

          <a className="text-12">
            Start your coding journey and start learning now from Beginner to
            Pro just in months with the help of <span>DEVSBASE</span> you will
            become a Frontend developer
          </a>
        </div>

        <div className="div-18">
          <img src={bannerthree} className="img-6" />

          <h3 className="text-13">Learn at home</h3>

          <a className="text-12">
            With <span>DEVSBASE</span> learning coding at home will be more easy
            and fun for Beginners . We give your all the courses you need and
            exercises to practice with...
          </a>
        </div>

        <div className="div-18">
          <img src={bannertwo} className="img-6" />

          <h3 className="text-13">Building projects</h3>

          <a className="text-12">
            Start Building projects to get more experience as a Frontend
            developer by using <span>DEVSBASE</span>
          </a>
        </div>
      </div>
    </div>
  );
}

