import React from "react";
import "../Userhome.css";
import Coursesicon from "../icons/video.png";
import exerciseicon from "../icons/exercises.png";
import recenticon from "../icons/recent.png";
import abouticon from "../icons/about.png";
import updateIcon from "../icons/updates.png";
import subicon from "../icons/sub.png";
import nexticon from "../icons/next-svgrepo-com.svg";
import { Link, useNavigate } from "react-router-dom";

function Header() {
  const navigate = useNavigate();

  const Logout = () => {
    window.localStorage.removeItem("isLogedIn");

    navigate("/");
  };

  return (
    <div className="userhome-nav">
      <div className="userhome-nav-items">
        <div className="userhome-div-3">
          <h3 className="userhome-text-1">DEVSBASE</h3>

          <h3 className="userhome-text-2">
            <span>Hi,</span> Taofeeq
          </h3>

          <a className="userhome-text-3">Your Inventory</a>
        </div>

        <div className="userhome-div-1">
          <Link className="userhome-div-2" to="/seriesPage">
            <img src={Coursesicon} className="userhome-img-1" />
            <a>Courses</a>
            <img src={nexticon} className="userhome-img-2" />
          </Link>

          <Link className="userhome-div-2" to="/exercises">
            <img src={exerciseicon} className="userhome-img-1" />
            <a>Exercises</a>
            <img src={nexticon} className="userhome-img-2" />
          </Link>

          <Link className="userhome-div-2" to="/recently">
            <img src={recenticon} className="userhome-img-1" />
            <a>Recently</a>
            <img src={nexticon} className="userhome-img-2" />
          </Link>

          <Link className="userhome-div-2" to="/Paymentpage">
            <img src={subicon} className="userhome-img-1" />
            <a>Subscription</a>
            <img src={nexticon} className="userhome-img-2" />
          </Link>

          <Link className="userhome-div-2" to="/updatespage">
            <img src={updateIcon} className="userhome-img-1" />
            <a>Updates</a>
            <img src={nexticon} className="userhome-img-2" />
          </Link>

          <Link className="userhome-div-2" to="/About">
            <img src={abouticon} className="userhome-img-1" />
            <a>About Us</a>
            <img src={nexticon} className="userhome-img-2" />
          </Link>

          <a onClick={() => Logout()} className="logout-text">
            Log Out
          </a>
        </div>
      </div>
    </div>
  );
}

export default Header;
