import React from "react";
import { Link } from "react-router-dom";
import "../App.css";
import banner from "../img/banner-1.png";
import mark from "../icons/check-svgrepo-com.svg";

function SectionTwo() {
  return (
    <React.Fragment>
      <div className="section-2">
        <a>Our Programmers Goal</a>
        <div className="div-6">
          <div className="div-7">
            <div className="div-9">
              <a>DEVSBASE</a>

              <div className="div-10">
                <img src={mark} className="img-5" />
                <h3 className="text-7">
                  Learn Coding from <span>Beginner</span> to pro
                </h3>
              </div>

              <div className="div-10">
                <img src={mark} className="img-5" />

                <h3 className="text-7">
                  Follow our <span>Course</span> to success
                </h3>
              </div>

              <div className="div-10">
                <img src={mark} className="img-5" />
                <h3 className="text-7">
                  Become a <span>frontend developer</span>
                </h3>
              </div>

              <div className="div-10">
                <img src={mark} className="img-5" />
                <h3 className="text-7">
                  <span>Build</span> Landing Pages
                </h3>
              </div>
            </div>
          </div>
          <div className="div-8">
            <img src={banner} className="img-4" />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default SectionTwo;
