import React from "react";
import { Link, useNavigate } from "react-router-dom";
import "../Register.css";
import axios from "axios";
import { useRef, useState, useEffect } from "react";
import showPassword from "../icons/eye-slash-svgrepo-com.svg";
import hidePassword from "../icons/eye-svgrepo-com.svg";

function SignUp() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const showpass = useRef();
  const showpassTwo = useRef();
  const showpassIcon = useRef();
  const hidepassIcon = useRef();
  const history = useNavigate();
  const showPassClick = () => {
    showpass.current.type = "text";
    showpassTwo.current.type = "text";
    hidepassIcon.current.style.display = "flex";
    showpassIcon.current.style.display = "none";
  };

  const hidePassClick = () => {
    showpass.current.type = "password";
    showpassTwo.current.type = "password";
    hidepassIcon.current.style.display = "none";
    showpassIcon.current.style.display = "flex";
  };

  async function submit(e) {
    e.preventDefault();

    try {
      await axios
        .post("http://localhost:3001/signup", {
          email,
          password,
          username,
        })
        .then((res) => {
          if (res.data == "exist") {
            alert("User already exist");
          } else if (res.data == "notexist") {
            history("/homepage", { state: { id: email } });
            // window.localStorage.setItem("isLogedIn", true);
          }
        })
        .catch((e) => {
          alert("wrong details");
          console.log(e);
        });
    } catch {
      console.log(e);
    }
  }

  return (
    <div className="home-signup-page">
      <form onSubmit={submit} className="form-signup-page">
        <h3 className="text-1-login-page">DEVSBASE SignUp</h3>

        <input
          type="text"
          placeholder="email"
          className="input-1-signup-page"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />

        <input
          type="text"
          placeholder="username"
          className="input-1-signup-page"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          required
        />

        <div className="div-1-login-page">
          <input
            type="password"
            placeholder="password"
            className="input-2-signup-page"
            ref={showpass}
          />
          <img
            src={showPassword}
            onClick={showPassClick}
            ref={showpassIcon}
            className="login-page-img-1"
          />
          <img
            src={hidePassword}
            onClick={hidePassClick}
            ref={hidepassIcon}
            className="login-page-img-2"
          />
        </div>

        <input
          type="password"
          placeholder="confirm password"
          className="input-1-signup-page"
          ref={showpassTwo}
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />

        <button type="submit" className="btn-1-login-page">
          SignUp
        </button>
      </form>

      <a className="text-3-login-page">
        I have an account <Link to="/login">Login</Link>
      </a>
    </div>
  );
}

export default SignUp;
