import React from "react";
import { Link } from "react-router-dom";
import "../Register.css";
import { useRef } from "react";
import showPassword from "../icons/eye-slash-svgrepo-com.svg";
import hidePassword from "../icons/eye-svgrepo-com.svg";

function Forgotpassword() {
  return (
    <div className="home-login-page">
      <form className="form-forgot-password-page">
        <h3 className="text-1-login-page">Forgot Password</h3>

        <input
          type="text"
          placeholder="send code to email"
          className="input-1-forgot-password-page"
        />

        <Link className="btn-1-login-page" to="/inputcode">
          send code
        </Link>
      </form>

      <a className="text-3-login-page">
        i know my password <Link to="/login">login</Link>
      </a>
    </div>
  );
}

export default Forgotpassword;
